import React from "react";
import * as utils from "../../utils/utils";
import useCheckin from "../../helper/useCheckin";

function HomeCheckinTile(props) {
    const { data, loading, error, checkin } = useCheckin({ hash: props.attendee.ticketHash, identifier: props.item.TidPid });

    const count = utils.countEntries(props.item.TidPid, props.attendee.data.checkIns);
    const rightValid = utils.validateRight(props.item, props.attendee.data.checkIns);
    const timeFramesCount = props.item.timeFrames.length;

    return (
        <div id={props.item.TidPid} className={"m-2 m-md-4 d-flex align-items-center flex-wrap justify-content-center " + (rightValid ? "" : "not-available")}>
            <div className="text text--white flex-grow-1">
                <p className="m-0 d-flex flex-wrap align-items-center">
                    <span className="text--lg">{props.item.Name}</span>
                    {(count > 0 || data?.status === "success") && <span className="green-tag m-1">Eingecheckt</span>}
                </p>
                <p className="text--sm fw-light m-0">
                    {count}/{props.item.maxEntries} {props.item.maxEntries === 1 ? "Eintritt" : "Eintritte"} | {timeFramesCount} {timeFramesCount === 1 ? "Zeitraum" : "Zeitrume"} hinterlegt
                </p>
                {error && (
                    <p className="text--red fw-bold">
                        <span>{error}</span>
                    </p>
                )}
            </div>
            <button disabled={rightValid ? "" : "disabled"} onClick={() => checkin(props.item.TidPid)} className="m-2 btn--secondary btn--lg">
                {loading ? <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span> : <span className="text-uppercase spacing--10"> Einchecken </span>}
            </button>
        </div>
    );
}

export default HomeCheckinTile;
