import React from "react";
import * as Zebra from "../../print/zebra";
import * as api from "../../helper/api";

function HomePrintTile(props) {
    function printBadge() {
        console.log("Print: " + props.item.url);
        Zebra.zebraPrinter
            .isPrinterReady()
            .then(() => {
                Zebra.sendImage(props.item.url);
                api.defaultBadgePrinted(props.item.hash)
                    .then((response) => console.log(response))
                    .catch((err) => console.log(err));
            })
            .catch(function (error) {
                console.error(error);
            });
    }

    return (
        <div className="col-12">
            <div className="m-2 m-md-4 d-flex flex-wrap justify-content-center align-items-center">
                <div className="text text--white flex-grow-1">
                    <p className="m-0 d-flex align-items-center">
                        <span className="text--lg">{props.item.name}</span>
                        {props.item.printed && <span className="green-tag m-1">Bereits gedruckt</span>}
                    </p>
                    <p className="text--sm fw-light m-0">
                        <a className="text text--white" data-bs-toggle="collapse" href={"#collapse" + props.id} role="button" aria-expanded="false" aria-controls={"collapse" + props.id}>
                            <span>Badge anzeigen</span>
                        </a>
                    </p>
                    <div className="collapse" id={"collapse" + props.id}>
                        <div className="card card-body m-3">
                            <img className="img-fluid" src={props.item.url} alt="" />
                        </div>
                    </div>
                </div>
                <button type={props.item.type} onClick={() => printBadge()} className="m-2 btn--secondary btn--lg">
                    <span className="text-uppercase spacing--10">Drucken</span>
                </button>
            </div>
        </div>
    );
}

export default HomePrintTile;
