export function formatDate(date) {
    var dt = new Date(date);
    return dt.toLocaleString();
}

export function validateRight(item, checkIns) {
    var valid = true;

    if (countEntries(item.TidPid, checkIns) >= item.maxEntries) {
        valid = false;
    }

    Object.entries(item.timeFrames).forEach(([key, value]) => {
        var from = new Date(value.validFrom);
        var till = new Date(value.validTill);
        if (!(from < Date.now() && Date.now() < till)) {
            valid = false;
        }
    });

    return valid;
}

export function countEntries(id, checkins) {
    var count = 0;
    Object.entries(checkins).forEach(([key, value]) => {
        if (value.ID === id) {
            count++;
        }
    });
    return count;
}
